gsap.registerPlugin(ScrollTrigger, InertiaPlugin);

Draggable.create(".home_project-list", {
  bounds: ".home_project-wrapper",
  //allowNativeTouchScrolling:false,
  type: "x",
  inertia: true,
  dragClickables: true
});

$(".home_project-item").on("click", function () {
  // record starting state
  const state = Flip.getState(
    ".home_project-item, .home_project-up, .home_project-title",
    {}
  );
  // make changes
  //$(this).siblings().find(".home_project-up").css("display", "none");
  $(this).addClass("is-active");
  $(this).siblings().addClass("is-active");
  // animate
  Flip.from(state, {
    duration: 1.5,
    absolute: ".home_project-item",
    ease: "power1.inOut"
  });
});
